<template>
  <div class="container">
    <app-bio
      :avatarUrl="avatarUrl"
      :name="userName"
      :githubName="githubName"
      :githubUrl="githubUrl"
    />

    <app-repos :repos-list="reposList" />
  </div>
</template>

<script>
import { getGithubData } from '@/api';
import AppBio from '@/components/AppBio.vue';
import AppRepos from '@/components/AppRepos.vue';

export default {
  name: 'App',
  components: {
    AppBio,
    AppRepos,
  },
  data() {
    return {
      githubData: {},
      userName: 'Artem Fedulov',
    };
  },
  computed: {
    avatarUrl() {
      return this.githubData.avatarUrl;
    },
    githubName() {
      return this.githubData.login;
    },
    githubUrl() {
      return this.githubData.url;
    },
    reposList() {
      return this.githubData.repos;
    },
  },
  async mounted() {
    this.githubData = await getGithubData();
  },
}
</script>

<style>
.container {
  width: 100%;
  max-width: 1000px;
  margin: auto;

  font-family: Arial, sans-serif;
}
</style>
