const USER_DATA_URL = 'https://api.github.com/users/arfedulov';

const get = async (url) => {
  const response = await fetch(url);
  if (response.ok) {
    return response.json();
  }
};

const getRepos = async (udata) => {
  if (udata.repos_url) {
    return (await get(udata.repos_url) || []).filter((repoData) => {
      return !repoData.fork && repoData.description;
    });
  }
};

const compareFunction = (a, b) => {
  if (a.stars < b.stars) {
    return 1;
  }
  if (a.stars === b.stars && a.createdAt < b.createdAt) {
    return 1;
  }
  return -1;
};

const repoDataMaper = (repoData) => {
  return {
    description: repoData.description,
    language: repoData.language,
    createdAt: repoData.created_at,
    stars: repoData.stargazers_count,
    forks: repoData.forks_count,
    name: repoData.name,
    url: repoData.html_url,
    siteUrl: repoData.homepage,
  };
};

export const getGithubData = async () => {
  const udata = await get(USER_DATA_URL);
  const repos = await getRepos(udata);

  return {
    avatarUrl: udata.avatar_url || '',
    url: udata.html_url,
    login: udata.login,
    repos: repos.map(repoDataMaper).sort(compareFunction),
  };
};
