<template>
  <div class="repo-card">
    <h2 class="header">
      <a class="link" :href="repo.url">
        <img class="icon" src="../assets/git.svg" />
        {{ repo.name }}
      </a>
    </h2>

    <p>
      {{ repo.description }}
    </p>

    <div class="footer">

      <span class="footer-item">
        <img class="icon" src="../assets/star.svg" />
        {{ repo.stars }}
      </span>

      <span v-if="repo.siteUrl" class="footer-item">
        <a :href="repo.siteUrl">
          <img class="icon" src="../assets/globe.svg" :title="repo.siteUrl" />
        </a>
      </span>

      <span class="footer-item language">
        <span class="lang-color" :style="langStyle"></span>
        {{ repo.language }}
      </span>

    </div>
  </div>
</template>

<script>
import LANG_COLORS from '@/lang-colors.json';

export default {
  name: 'RepoCard',
  props: {
    repo: Object,
  },
  computed: {
    langStyle() {
      return { backgroundColor: LANG_COLORS[this.repo.language] || '#000000' };
    },
  },
}
</script>

